.brand {
  color: white;
  font-size: 2.5rem;
  font-weight: bold;
}

/* dropdown */
.dropdown {
  display: inline-block;
  position: relative;
}
.dropdown-content {
  position: absolute;
  display: none;
  /* right: 0; */
  min-width: 12rem;
  padding: 0.5rem 1rem;
  z-index: 1;
  background-color: #203040;
  /* margin: 0; */
  margin-top: 0.4rem;
  margin-left: -6rem;
  border-radius: 0.5rem;
}
.dropdown:hover .dropdown-content {
  display: block;
}

/* Search */
.search button {
  width: 4rem;
  border-radius: 0 0.5rem 0.5rem 0;
  border-right: none;
  margin-right: 0.5rem;
}

.search input {
  border-radius: 0.5rem 0 0 0.5rem;
  border-right: none;
  margin-left: 0.5rem;
}
.search input:hover {
  cursor: text;
}
